export default {
	data(){
		return {
			src:'',
			cameraSrc:{
				type:String,
				default:''
			}
		}
	},
	components:{
	},
	methods:{
		error(e){
			console.log(e);
		},
		takePhoto(){
			// 获取图片的上下文
			const ctx = uni.createCameraContext();
			ctx.takePhoto({
				success:function(e){
					console.log(e);
					this.src = e.tempImagePath;
				}
			})
		},
		
		getAlbum(){
			console.log(this);
			uni.chooseImage({
				complete:function(e){
					console.log(this);
				},
				// 常用的bug 此时赋值需要用箭头函数，如果不用箭头函数，会访问不到cameraSrc 会报undefined
				// 需要知道this的指向问题。 上面comlete中的this，就会指向这个函数。
				success:(e)=>{
					this.cameraSrc =e.tempFilePaths[0];
					console.log(this);
				},
				
				fail:function(err){
					console.log(err);
				}
			})
		},
		err(e){
			console.log(e);
		}
	}
}
